.move {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    gap: 0.5rem;
    border: 1px solid;
    border-radius: 0.5rem;
    padding: 1rem;
    cursor: pointer;

    &.favourite {
        border: 1px solid gold;
    }

    &__main {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-start;
        width: 100%;
        gap: 0.5rem;

        &__name {
            display: flex;
            width: 55%;
            font-size: 1.5rem;
            font-weight: 600;

            &.favourite {
                color: gold;
            }
        }

        &__badges {
            display: flex;
            flex-direction: row;
            gap: 0.5rem;

            .small {
                min-width: 2.5rem;
                min-height: 2.5rem;
                height: 2.5rem;
                width: 2.5rem;
                font-size: 1.2rem;
                padding: 0;
            }

            .favourite {
                color: black;
                background-color: gold;
            }
        }

    }

    &__category {
        display: flex;
        gap: 0.5rem;
        margin-bottom: 0.5rem;

        .dodge {
            min-width: 2.5em;
            width: 2.5em;
            min-height: 2.5rem;
            height: 2.5rem;
        }
    }

    &__props {
        display: flex;
        flex-direction: row;
        align-items: end;
        text-align: center;
        flex-wrap: wrap;
        box-sizing: border-box;
        gap: 0.3rem;
        font-size: 1.2rem;
        margin-bottom: 0.5rem;
    }

    .move-command {
        box-sizing: border-box;
        border: 1px solid white;
        border-radius: 0.5rem;
        padding: 0.7rem 0.7rem;

    }

    &__notes {
        font-size: 1.2rem;
        font-style: italic;
    }
}